import {IndexModel} from '../../model/index.js'
const  indexModel = new IndexModel()
    export default {
        name: "choice_store",
        data(){
            return {
                shops:[]
            }
        },
        methods:{
            getShop()
            {
                let self = this
                uni.getLocation({
                   type: 'wgs84',
                    success: function (res) {
                        let params = {
                            longitude:res.longitude,
                            latitude:res.latitude,
                        }
                        indexModel.getShopList(params).then((res) => {
                            self.shops = res.data
                        })
                    }
                }); 
            }
        },
        onLoad(){
            this.getShop()
        }
    }
